.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Header.js CSS */

.clip-text {
    font-size: 6em;
    font-weight: bold;
    line-height: 1;
    position: relative;
    display: inline-block;
    margin: .25em;
    padding: .7em .70em;
    text-align: center;

    /* Color fallback */
    color: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
@media only screen and (max-width: 815px) {
  .clip-text {
      font-size: 4em;
      font-weight: bold;
      line-height: 1.2;
      position: relative;
      display: inline-block;
      margin: .20em;
      padding: .6em .50em;
      text-align: center;

      /* Color fallback */
      color: #fff;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

  }
}

.clip-text:before,
.clip-text:after {
    position: absolute;
    content: '';

}

/* Background */
.clip-text:before {
    z-index: -2;
    top: 0;
    right: 0;
    bottom: 0;
    left: -0;
    background-image: inherit;
    object-fit: cover;
    background-size: cover;
}

/* Text Background (black zone) */
.clip-text:after {
    position: absolute;
    z-index: -1;
    top: .220em;
    right: 0;
    bottom: .15em;
    left: .225em;
    background-color: #fff;

}

@media only screen and (max-width: 700px) {
  .clip-text:after {
      position: absolute;
      z-index: -1;
      top: .150em;
      right: 0;
      bottom: .35em;
      left: .160em;
      background-color: #fff;

  }
}



