/* Contact.js */
.icons img{
  -webkit-filter: hue-rotate(50deg) saturate(3);
   filter: hue-rotate(50deg) saturate(3);
}
.icons img:hover{
  -webkit-filter: hue-rotate(190deg) saturate(3);
   filter: hue-rotate(190deg) saturate(3);
  transform: scale(1.1);

}

/* PhotoScroll.js */
.gridItems{
  scrollbar-width: none;
 -ms-overflow-style: none;
}
.gridItems::-webkit-scrollbar {
  display: none;
}
.imghover {
    cursor: pointer;
}

